import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {extensionSplit} from '@servicenow/library-enhanced-utils';
import styles from './_now-heading.scss';

const view = (state) => {
	const {label, level, variant, wontWrap, hasNoMargin} = state.properties;
	// eslint-disable-next-line no-unused-vars
	const HeadingLevel = `h${level}`;
	return (
		<HeadingLevel
			class={{
				'now-heading': true,
				...extensionSplit(variant),
				'has-no-margin': hasNoMargin
			}}
			title={wontWrap ? label : undefined}>
			<span className="now-line-height-crop">
				{wontWrap ? <span className="wont-wrap">{label}</span> : label}
			</span>
		</HeadingLevel>
	);
};

/**
 * Use a heading to communicate the hierarchy of content on your page. Set heading
 * levels according to the logical structure of the document, then apply
 * visual styles separately. Visually styling headers according
 * to the structure of your page also improves the readability and scannability
 * of your design.
 *
 * ```jsx
 * <now-heading label="Default" />
 *
 * <now-heading label="Header Primary" level="1" variant="header-primary" />
 *
 * <now-heading label="Header Tertiary" level="3" variant="header-tertiary" wont-wrap />
 * ```
 *
 * @seismicElement now-heading
 * @summary Text that provides a short summary of content, styled to indicate the hierarchy of content on a page.
 * @uib.label Heading
 * @uib.icon heading
 * @uib.description Displays heading text in multiple sizes
 * @uib.category primitives
 * @uib.associatedTypes global.core, global.dashboard, global.landing-page
 * @uib.properties label, level, variant, wontWrap, hasNoMargin
 */
createEnhancedElement('now-heading', {
	properties: {
		/**
		 * Defines the textual content rendered by the header.
		 * @type {string}
		 * @uib.required
		 * @uib.description Text for the heading
		 * @uib.defaultValue We make work, work better for people with modern digital workflows
		 * @uib.translatable true
		 */
		label: {required: true, schema: {type: 'string'}},
		/**
		 * Defines the HTML <hx> heading element used to render the label.
		 * @type {('1'|'2'|'3'|'4'|'5'|'6')}
		 * @uib.description Defines the HTML <hx> heading element used to render the label
		 */
		level: {
			default: '1',
			schema: {type: 'string', enum: ['1', '2', '3', '4', '5', '6']}
		},
		/**
		 * Defines the styles used to render the heading.
		 * @type {('header-primary'|'header-secondary'|'header-tertiary'|'title-primary'|'title-secondary'|'title-tertiary')}
		 * @uib.label Style
		 * @uib.description Text style
		 */
		variant: {
			default: 'header-primary',
			schema: {
				type: 'string',
				enum: [
					'header-primary',
					'header-secondary',
					'header-tertiary',
					'title-primary',
					'title-secondary',
					'title-tertiary'
				]
			}
		},
		/**
		 * Set to true if the heading should have no block end margin.
		 * @type {boolean}
		 * @uib.label Hide bottom margin
		 * @uib.description Hides the extra space on the bottom of the text
		 */
		hasNoMargin: {default: false, schema: {type: 'boolean'}},
		/**
		 * Set to true if the heading label should not wrap. Label text is
		 * truncated if it does not fit in the available space. The full label is
		 * rendered in a title attribute.
		 * @type {boolean}
		 * @uib.label Disable text wrap
		 * @uib.description Truncates the text with an ellipsis if it does not fit on one line
		 */
		wontWrap: {default: false, schema: {type: 'boolean'}}
	},
	view,
	styles
});
